import request from "./request"

const baseUrl = 'https://elm.cangdu.org'

//
export const locationApi = param => request.get(baseUrl + "/v2/pois/" + param);

//
export const foodListApi = param => request.get("http://kumanxuan1.f3322.net:8001/v2/index_entry" + param)

//
export const storeListApi = param => request.get(baseUrl + "/shopping/restaurants", { params: param })

//
export const storeInfoApi = param => request.get(baseUrl + "/shopping/restaurant/" + param);

//API
export const storeFoodListApi = param => request.get(baseUrl + "/shopping/v2/menu", { params: param })

//API
export const foodStartApi = param => request.get(`${baseUrl}/ugc/v2/restaurants/${param}`)
//
export const commentListApi = ({ url, option }) => request.get(`${baseUrl}/ugc/v2/restaurants/${url}`, { params: option })
